  import Citys from './citys'
  export default {
    props: {
      hotCitys: {
        type: Array,
        default () {
          return []
        }
      },
      value: {
        type: String,
        default: '未设置'
      }
    },
    data() {
      return {
        citys: Citys,
        windowHeight: '',
        scrollIntoId: 'F',
        current: this.value,
		listCurID: '',
		hidden:true,
		listCur: ''
      }
    },
    mounted() {
      this.getSystemInfo()
	  let that = this;
	  uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
	  	that.boxTop = res.top
	  }).exec();
	  uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
	  	that.barTop = res.top
	  }).exec()
    },
	onReady() {
		
	},
    methods: {
		//获取文字信息
		getCur(e) {
			this.hidden = false;
			this.listCur = this.citys[e.target.id].letter;
		},
		setCur(e) {
			this.hidden = true;
			this.listCur = this.listCur
		},
		//滑动选择Item
		tMove(e) {
			let y = e.touches[0].clientY,
				offsettop = this.boxTop,
				that = this;
			//判断选择区域,只有在选择区才会生效
			if (y > offsettop) {
				let num = parseInt((y - offsettop) / 20);
				this.listCur = that.citys[num].letter
			};
		},
		
		//触发全部开始选择
		tStart() {
			this.hidden = false
		},
		
		//触发结束选择
		tEnd() {
			this.hidden = true;
			this.listCurID = this.listCur
		},
      getSystemInfo() {
        uni.getSystemInfo().then(res => {
          let [error, data] = res
          this.windowHeight = `${data.windowHeight}px`
        })
      },
      scrollTo(letter) {
        this.scrollIntoId = letter === '#' ? 'current' : letter
      },
      onSelect(city) {
        this.current = city
        this.$emit('input', city)
        this.$emit('on-select', city)
      }
    }
  }
